.profile_institute{
    background-color: #0a382b;
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;
}
.profile_institute section .form-container h3{
    color: white;
    font-size: 25px;
    letter-spacing: 1px;
    border-bottom: 3px solid white;
    margin:0px auto 20px auto;
    width: 300px;
}
.profile_institute section header{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2AF6BC;
    padding: 10px 15px;
    width: 100%;
    text-align: center;
    border-radius: 15px;
    position: relative;
    border: 1px solid #707070;
    opacity:0.68;
}

.profile_institute section{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 600px;
    position: absolute;
    right: 200px;
    top:50px;
}

.profile_institute section header .icon{
    position: absolute;
    left: 20px;
    bottom: 5px;
}
.profile_institute section header .text{
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 800;
}
.profile_institute .back {
    position: absolute;
    right: 20px;
    top: 20px;
}
.profile_institute .back img{
    width: 30px;
    height: 30px;
}
.profile_institute .back span{
    border: 2px solid white;
    padding: 5px 55px 5px 5px;
    position: relative;
    right: -50px;
    border-radius: 20px 0% 0% 20px;
    color: white;
    opacity: 0;
    visibility: hidden;
    transition: all 0.6s ease;
    border-right: none;
}
.back:hover.back span{
    opacity: 1; 
    visibility: visible;
}

.profile_institute .section2 img{
    position: absolute;
    left:0;
    top:0px;
    width: 27%;
    height: 100%;
}

.profile_institute section .form-container{
    background-color: #22594e;
    width: 100%;
    text-align: center;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border-radius: 50px;
    border: 1px solid #707070;
    height: 480px;
}
.profile_institute section .body .information{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 3px solid #c4f9ae;
    width: 80%;
    margin: 15px auto;
    border-radius: 15px;
    padding: 0px 10px;
}
.profile_institute section .body .information input{
    width: 100%;
    height: 100%;
    padding: 10px;
    background:none;
    border: none;
    color:#fff;
    font-size: 20px;
}
.profile_institute section .body .information input:focus{
    outline:none ;
}
.profile_institute section .body .information img{
    width: 25px;
}
.profile_institute section .body .information p{
    font-size: 20px;
    text-align: left;
    color: white;
    position: relative; 
    top:6px 
}

.profile_institute section .footer .next{
    text-align: right;
    background-color: #2AF6BC;
    border: none;
    border-radius: 30px;
    font-size: 18px;
    position: relative;
    border: 2px solid #fff;
    transition: all 0.5s ease;
    opacity: 0.68;
}

/*----------------------------------*/
@media only screen and (max-width: 1150px){
    .profile_institute section{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 550px;
        position: absolute;
        right: 150px;
        top:50px;
    }
}

@media only screen and (max-width: 1050px){
    .profile_institute .section2 img{
        display: none;
    }
    .profile_institute section{
        position:absolute;
        left:50%;
        transform: translate(-50%);
    }
}
@media only screen and (max-width: 680px){
    .profile_institute .back {
        display: none;
    }
    .profile_institute section{
        position:absolute;
        left:50%;
        transform: translate(-50%);
        width:450px;
    }
    .profile_institute section .form-container{
        border-radius: 40px;
    }
}
@media only screen and (max-width: 480px){
    .profile_institute section{
        position:absolute;
        left:50%;
        transform: translate(-50%);
        width:380px;
    }
    .profile_institute section .body .information{
        width: 95%;
    }
}
@media only screen and (max-width: 410px){
    .profile_institute section{
        position:absolute;
        left:50%;
        transform: translate(-50%);
        width:330px;
    }
    .profile_institute section .form-container{
        border-radius: 30px;
    }
}